@import "../../styles/mixins/media.scss";

.contactList {
  margin: 40px 0;
  @include desktop {
    display: flex;
    margin: 0;
  }
}

.telLink {
  color: var(--white);
  text-decoration: none;
  transition: var(--defaultTransition);

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.socialNetworksList {
  margin-top: 30px;
  @include desktop {
    margin-top: 0;
    margin-left: 30px;
  }
}

.langBlock {
  display: flex;
  align-items: center;
  justify-content: baseline;
  color: var(--white);
  margin-top: 30px;
  @include desktop {
    margin-top: 0;
    margin-left: 30px;
  }
}

.langBtn {
  background-color: transparent;
  border: none;
  padding: 0;
  color: var(--white);
  font-size: 14px;
  line-height: 15.6px;
  cursor: pointer;
  transition: var(--defaultTransition);

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.ukr {
  margin-right: 9px;
}

.ukr::after {
  content: "|";
  margin-left: 9px;
}

.userBlock {
  color: var(--white);
  display: flex;
  align-items: center;
  margin-top: 30px;

  @include desktop {
    margin-top: 0;
    margin-left: 30px;
  }
}

.link {
  display: flex;
  color: var(--white);
  text-decoration: none;
}

.icon {
  margin-right: 10px;

  transition: var(--defaultTransition);

  &:hover,
  &:focus {
    transform: scale(1.2);
  }
}

.text {
  align-self: center;
  transition: var(--defaultTransition);

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}
