@import "../../styles/mixins/media.scss";

.header {
  background-color: var(--accent);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60px;

  @include tablet {
    height: 75px;
  }

  @include desktop {
    height: 150px;
  }
}

.container {
  width: 328px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include largeMobile {
    min-width: 448px;
  }

  @include tablet {
    min-width: 708px;
  }

  @include largeTablet {
    min-width: 940px;
  }

  @include desktop {
    position: relative;
    min-width: 1230px;
    flex-direction: column;
  }
}

.upContainer {
  @include desktop {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 36px;
  }
}

.leftContainer {
  display: flex;
  align-items: center;
}

.burgerMenuBtn {
  background-color: transparent;
  border: none;
  padding: 0;
}

.burgerMenu {
  margin-right: 10px;

  @include tabletAndDesktop {
    margin-right: 30px;
  }

  @include desktop {
    display: none;
  }
}

.logo {
  transition: var(--defaultTransition);
  @include tabletAndDesktop {
    width: 138px;
    height: 32px;
  }
  &:hover,
  &:focus {
    transform: scale(1.1);
  }
}

.downContainer {
  @include tablet {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  @include desktop {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 264px;
    width: 100%;
  }
}

.catalogContainer {
  display: none;

  @include desktop {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: -16px;
    left: 0;
    width: 234px;
    height: 79px;
    background-color: #ffeaf1;
    border-radius: 20px 20px 0px 0px;

    transition: var(--defaultTransition);

    &:hover {
      background-color: var(--white);

      &::after {
        content: "";
        position: absolute;
        top: -9px;

        height: 174px;
        width: 1px;
        background-color: #c7c7c7;
        transform: rotate(90deg);
      }
    }
  }
}

.btnMenu {
  background-color: transparent;
  border: none;

  cursor: pointer;
}

.catalogListWrapper {
  position: absolute;
  padding: 0 30px;
  top: 79px;
  width: 234px;
  background-color: var(--white);
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.07);
}

.text {
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 19px;
  text-transform: uppercase;
  text-decoration: none;
  margin-left: 10px;

  color: var(--black);
}

.inputContainer {
  position: relative;
  min-width: 250px;
  display: none;

  @include largeTablet {
    min-width: 480px;
  }

  @include desktop {
    min-width: 797px;
  }

  @include tabletAndDesktop {
    display: flex;
  }
}

.input {
  background-color: rgba(255, 255, 255, 0.15);
  color: var(--white);
  padding-left: 15px;
  height: 47px;
  width: 100%;
  border: 1px solid var(--white);
  border-radius: 10px;

  &::placeholder {
    color: var(--white);
  }

  &:hover {
    background-color: transparent;
  }

  &:focus {
    background-color: rgba(255, 255, 255, 0.15);
  }
}

.srchBtn {
  position: absolute;
  top: 15px;
  right: 15px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding: 0;
  transition: var(--defaultTransition);

  &:hover,
  &:focus {
    transform: scale(1.2);
  }
}

.navbar {
  display: none;

  @include desktop {
    display: flex;
  }
}

.rightContainer {
  display: none;

  @include desktop {
    display: flex;
  }
}

.closeBtn {
  z-index: 1000;
  position: fixed;
  top: 140px;
  right: 16px;
  background-color: transparent;
  padding: 0;
  border: none;
  width: 25px;
  height: 25px;
}
