.link {
  margin-right: 16px;
  color: var(--black);
  text-decoration: none;

  &:hover {
    .name {
      font-style: italic;
    }
  }
}

.img {
  display: block;
  width: 156px;
  height: 156px;
  margin-bottom: 13px;
}

.name {
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 13px;
}

.oldPrice {
  color: rgba(32, 32, 32, 1);
  font-size: 12px;
  line-height: 13.4px;
  font-weight: 700;
  text-decoration: line-through;
  margin-bottom: 5px;
}

.imgWrapper {
  position: relative;
}

.price {
  color: rgba(249, 156, 17, 1);
  font-size: 15px;
  line-height: 17px;
}

.sellIcon {
  position: absolute;
  background-color: rgba(249, 156, 17, 1);
  color: var(--white);
  border-radius: 5px;
  height: 30px;
  padding: 7px;
}
