@import "../../styles/mixins/media.scss";

.lists {
  @include tabletAndDesktop {
    display: flex;
  }
}

.infoWrappper {
  @include tablet {
    margin-right: 50px;
  }

  @include desktop {
    margin-right: 100px;
  }
}

.infoList {
  margin-bottom: 35px;

  @include tablet {
    margin-right: 30px;
  }

  @include desktop {
    margin-right: 50px;
  }
}

.item {
  font-size: 14px;
  font-weight: 300;
  line-height: 16px;
}

.item:not(:last-child) {
  margin-bottom: 15px;
}

.link {
  text-decoration: none;
  color: var(--white);

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.catalogLists {
  display: flex;
  justify-content: space-between;
}
