.menuWrapper {
  background-color: var(--accent);
  position: fixed;
  top: 0;
  left: 0;

  margin: 0 auto;
  z-index: 10;
  width: 100vw;
  height: 100vh;
  overflow: auto;
  padding-top: 30px;
}

.catalogContainer {
  display: flex;
  align-items: center;
  width: 234px;
  height: 80px;
  background-color: rgba(255, 234, 241, 1);
  border-radius: 20px;
}

.text {
  font-style: normal;
  font-weight: 500;
  text-decoration: none;
  font-size: 17px;
  line-height: 19px;
  text-transform: uppercase;

  color: var(--black);
}

.navBarContainer {
  display: flex;
  justify-content: space-between;
}

.catalogListWrapper {
  background-color: var(--white);
  position: fixed;
  top: 0;
  left: 0;
  margin: 0 auto;
  z-index: 100;
  min-width: 479px;
  height: 100vh;
  overflow: auto;
  padding-top: 30px;
}

.btn {
  background-color: transparent;
  margin: 30px 10px 30px 30px;
  padding: 0;
  border: none;
  cursor: pointer;
}

.title {
  font-size: 17px;
  line-height: 19px;

  margin-left: 10px;
}

.btnBack {
  background-color: transparent;
  border: none;
  padding: 0;

  cursor: pointer;
}
