@import "../../styles/mixins/media.scss";

.ulNavbar {
  margin-top: 40px;

  &::after {
    content: "";
    background-color: rgba(255, 197, 214, 1);
    height: 1px;
    width: 100%;
    display: block;
    margin-top: 40px;
  }

  @include desktop {
    display: flex;
    margin-top: 0;

    &::after {
      display: none;
    }
  }
}

.item:not(:last-child) {
  margin-bottom: 30px;
  @include desktop {
    margin-bottom: 0;
  }
}

.item {
  @include desktop {
    margin-left: 20px;
  }
}

.link {
  text-decoration: none;
  color: var(--white);
  transition: var(--defaultTransition);

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}
