@import "../../styles/mixins/media.scss";

.footer {
  @include desktop {
    background-color: var(--accent);
  }
}

.subscriptionBlockWrapper {
  background-color: rgba(249, 156, 17, 1);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 40px 0;

  @include tablet {
    background-color: var(--white);
    background-image: url("../../assets/images/footer/tabletFooter.png");
    padding: 0;
  }

  @include desktop {
    height: 260px;
    background-color: var(--white);
    background-image: url("../../assets/images/footer/desktopFooter.png");
    padding: 0;
  }
}

.subscriptionBlock {
  display: flex;
  justify-content: space-between;
}

.wrapperForElements {
  @include tablet {
    padding-top: 80px;
  }

  @include desktop {
    display: flex;
    align-items: center;
    padding-top: 120px;
  }
}

.inputWrapper {
  max-width: 335px;
}

.inputWrapper {
  display: flex;
  align-items: center;
}

.input {
  height: 50px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.15);
  border: 1px solid var(--white);
  color: var(--white);
  border-radius: 10px;

  margin-right: 15px;
  padding-left: 15px;

  &::placeholder {
    color: rgba(255, 255, 255, 1);
    font-size: 14px;
  }

  @include largeMobile {
    width: 265px;
  }

  @include tablet {
    width: 300px;
  }
}

.btn {
  padding: 0;
  background-color: var(--white);
  border: none;
  border-radius: 50px;
  min-width: 48px;
  height: 48px;
  cursor: pointer;

  &:hover,
  &:focus {
    border: 1px solid var(--white);
    background-color: rgba(255, 255, 255, 0.15);
  }
}

.image {
  display: none;

  @include tabletAndDesktop {
    display: block;
  }
}

.infoBlockWrapper {
  @include desktop {
    display: flex;
    max-width: 1290px;
    margin: 0 auto;
    padding-top: 60px;
  }
}

.infoBlock {
  background-color: var(--accent);
  padding: 40px 0;

  @include desktop {
    padding: 0;
    margin-left: 140px;
  }
}

.addInfoBlock {
  background-color: var(--accent);
  margin-top: -5px;
  padding-bottom: 40px;

  @include desktop {
    padding-bottom: 60px;
    margin-top: 40px;
  }
}
.copyright {
  color: var(--white);
  font-size: 14px;
  font-weight: 300;
}

.paymentSystems {
  display: flex;
  margin-top: 12px;
}
.itemImg:not(:last-child) {
  margin-right: 10px;
}

.img {
  border-radius: 10px;
}
