@import "../../styles/mixins/media.scss";

.headerBtnsContainer {
  display: flex;
}

.item {
  &:not(:first-child) {
    margin-left: 20px;
  }

  @include tabletAndDesktop {
    margin-left: 30px;

    &:nth-last-child(n + 4) {
      display: none;
    }
  }

  transition: var(--defaultTransition);

  &:hover,
  &:focus {
    transform: scale(1.2);
  }
}

.link {
  display: flex;
  text-decoration: none;
}

.counter {
  display: none;

  @include tabletAndDesktop {
    color: var(--white);
    display: flex;
    margin-left: 7px;
    font-size: 15px;
  }
}
